/** Custom Select **/
.custom__select-wrapper {
  width: 50px;
  min-width: 50px;
  position: relative;
  display: inline-block;
  user-select: none;
}

.custom__select-wrapper select {
  display: none;
}

.custom__select {
  position: relative;
  display: inline-block;
}

.custom__select-trigger {
  width: 50px;
  height: 26px;
  position: relative;
  display: block;
  padding-right: 26.5px;
  cursor: pointer;
  white-space: nowrap;
  background-repeat: no-repeat;
  background-size: 25.5px 25.5px;
  background-position: left center;
  background-image: url(../img/lang-usa.svg);
}

.custom__select-trigger:after {
  position: absolute;
  top: 50%;
  right: 0;
  display: block;
  content: '';
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 6.2px 6.2px 0 6.2px;
  border-color: #ffffff transparent transparent transparent;
  transition: all .4s ease-in-out;
  transform-origin: 50% 0;
}

.custom__select.opened .custom__select-trigger:after {
  transform: rotate(-180deg) translateY(-50%);
}

.custom__options {
  position: absolute;
  display: block;
  top: 100%;
  left: 0;
  right: 0;
  width: 100%;
  margin: 10px 0;
  box-sizing: border-box;
  transition: all .2s ease-in-out;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-10px);
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 2px;
  z-index: 101;
}

.custom__select.opened .custom__options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(0);
}

.custom__option {
  position: relative;
  display: block;
  font-size: 12px;
  padding: 11px 10px;
  cursor: pointer;
  transition: all .2s ease-in-out;
}

.custom__option:first-of-type {
  border-radius: 2px 2px 0 0;
}

.custom__option:last-of-type {
  border-bottom: 0;
  border-radius: 0 0 2px 2px;
}

.custom__option:hover {
  background-color: rgba(249, 157, 11, 0.3);
}

.custom__option.selection {
  background-color: #F99D0B;
}

.custom__option {
  height: 16px;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  background-position: center;
}

[data-value="usa"] {
  background-image: url(../img/lang-usa.svg);
}

