@import "reset";
@import "mixins";
@import "vars";
@import "generated/sprite-svg";
html,
body {
  width: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}

// Common styles
a {
  color: inherit;
  transition: all 0.2s ease;
}

.wrapper {
  position: relative;
  font-family: $font-primary;
  line-height: 1.6;
  @include rfs(15);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1170px;
  width: 100%;
  @media only screen and (max-width: 1170px) {
    padding-left: 1%;
    padding-right: 1%;
  }
}

.text-center {
  text-align: center;
}

.circle {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 12px;
  width: 28px;
  height: 28px;
  min-width: 28px;
  min-height: 28px;
  border-radius: 50%;
  background-color: #fff;
  img {
    max-width: 100%;
  }
}

.phone__icon {
  width: 16px;
  height: 16px;
}

// end Common styles
// header
.header {
  min-height: 57px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 101;
  color: #fff;
  background-color: rgba(0, 0, 0, .3);
  &-inner {
    padding: 13px 0 9px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media only screen and (max-width: 480px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__logo {
    display: flex;
    max-width: 164px;
    height: 1em;
    margin-right: 10px;
    @include rfs(35);
    img {
      display: block;
      min-width: 100px;
      width: 100%;
    }
  }
  &__right {
    display: flex;
    align-items: center;
    @media only screen and (max-width: 480px) {
      margin-top: 10px;
    }
  }
  &__phone {
    display: flex;
    align-items: center;
    @include rfs(22);
    font-weight: bold;
    color: #ffffff;
    @media only screen and (max-width: 768px) {
      @include rfs(16);
    }
    &:hover {
      color: #f99d0b;
    }
    .phone__circle {
      margin-right: 13px;
    }
    a {
      white-space: nowrap;
    }
  }
  &__lang {
    margin-left: 5vw;
    @media only screen and (max-width: 768px) {
      margin-left: 4vw;
    }
  }
}


/** Custom Select **/

.custom__select-wrapper {
  width: 50px;
  min-width: 50px;
  position: relative;
  display: inline-block;
  user-select: none;
}

.custom__select-wrapper select {
  display: none;
}

.custom__select {
  position: relative;
  display: inline-block;
}

.custom__select-trigger {
  width: 50px;
  height: 26px;
  position: relative;
  display: block;
  padding-right: 26.5px;
  cursor: pointer;
  white-space: nowrap;
  background-repeat: no-repeat;
  background-size: 25.5px 25.5px;
  background-position: left center;
  background-image: url(../img/lang-usa.svg);
}

.custom__select-trigger:after {
  position: absolute;
  top: 50%;
  right: 0;
  display: block;
  content: '';
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 6.2px 6.2px 0 6.2px;
  border-color: #ffffff transparent transparent transparent;
  transition: all .4s ease-in-out;
  transform-origin: 50% 0;
}

.custom__select.opened .custom__select-trigger:after {
  transform: rotate(-180deg) translateY(-50%);
}

.custom__options {
  position: absolute;
  display: block;
  top: 100%;
  left: 0;
  right: 0;
  width: 100%;
  margin: 10px 0;
  box-sizing: border-box;
  transition: all .2s ease-in-out;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-10px);
  background-color: rgba(255, 255, 255, .8);
  border-radius: 2px;
  z-index: 101;
}

.custom__select.opened .custom__options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(0);
}

.custom__option {
  position: relative;
  display: block;
  font-size: 12px;
  padding: 11px 10px;
  cursor: pointer;
  transition: all .2s ease-in-out;
}

.custom__option:first-of-type {
  border-radius: 2px 2px 0 0;
}

.custom__option:last-of-type {
  border-bottom: 0;
  border-radius: 0 0 2px 2px;
}

.custom__option:hover {
  background-color: rgba(249, 157, 11, .3);
}

.custom__option.selection {
  background-color: #F99D0B;
}

// end custom select
.custom__option {
  height: 16px;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  background-position: center;
}

[data-value="usa"] {
  background-image: url(../img/lang-usa.svg);
}

// end header
// hero
.hero {
  position: relative;
  padding-bottom: 8em;
  color: #ffffff;
  background-color: #3a3a3a;
  background-size: cover;
  background-position: center center;
  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 8.3em 100vw;
    border-color: transparent transparent #fff transparent;
  }
  @media only screen and (max-width: 992px) {
    padding-bottom: 10vw;
    &:after {
      border-width: 0 0 10vw 100vw;
    }
  }
  &__why {
    position: relative;
    padding-top: 3.7em;
    padding-bottom: 0.24em;
    font-family: $font-primary;
    @include rfs(50);
    font-weight: bold;
    color: #ffffff;
    @media only screen and (max-width: 480px) {
      padding-top: 4em;
    }
    &:after {
      content: "";
      position: absolute;
      bottom: -3px;
      left: 0;
      display: block;
      width: 50px;
      height: 2px;
      background-color: #ffffff;
    }
  }
}

.content {
  position: relative;
  padding-top: 5.6em;
  padding-bottom: 6em;
  &__row {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @media only screen and (max-width: 768px) {
      justify-content: center;
    }
  }
  &__column {
    flex-basis: 49%;
    margin-bottom: 1em;
    @media only screen and (max-width: 768px) {
      flex-basis: 100%;
    }
  }
  &__picture {
    display: block;
    width: 100%;
    max-width: 510px;
    margin-top: -17px;
    margin-left: auto;
  }
  &__title {
    font-family: $font-primary;
    @include rfs(38);
    line-height: 1.1;
    font-weight: bold;
    color: #0b5b8a;
  }
  &__title--underlined {
    position: relative;
    padding-bottom: 1em;
    &:after {
      content: "";
      position: absolute;
      bottom: 0.35em;
      left: 0;
      display: block;
      width: 50px;
      height: 2px;
      background-color: #0b5b8a;
    }
  }
  &__text {
    padding-top: 10px;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    color: #373737;
  }
  // feedback ratings
  .content__feedback {
    max-width: 570px;
    padding: 24px 50px 30px;
    margin-bottom: 3.22em;
    color: #373737;
    background-color: #fff;
    border-radius: 2px;
    @media only screen and (max-width: 1200px) {
      padding: 20px 2vw 30px;
    }
  }
  .feedback__ratings-wrap {
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    font-size: 15px;
    .reviews__star {
      width: 13.3px;
      height: 12.7px;
    }
  }
  .rating__caption {
    font-size: 14px;
    font-weight: 500;
    text-align: center;
    text-transform: uppercase;
  }
  .feedback__text {
    padding-top: 1em;
    line-height: 1.8;
    font-weight: 300;
  }
  .feedback__footer {
    margin-top: 8px;
  }
  .feedback__imre {
    margin-right: 7px;
    font-size: 20px;
    font-weight: bold;
    color: #0b5b8a;
  }
  .feedback__author {
    font-weight: 300;
  }
}

.content--skewed {
  padding-bottom: 4.7em;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    height: 280px;
    width: 100%;
    transform: skew(0deg, 5deg);
    background-color: #EBF2F5;
    z-index: -1;
  }
  @media only screen and (max-width: 768px) {
    padding-top: 2em;
    &:before {
      height: 80px;
    }
  }
  .content__row {
    margin-top: 3.53em;
  }
}

// contact section
.info {
  min-height: 340px;
  color: #ffffff;
  background-color: #006f9c;
  &-inner {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    width: 100%;
    @media only screen and (max-width: 992px) {
      flex-wrap: wrap;
      flex-direction: column;
      align-items: center;
      .reviews {
        order: 1;
        margin-bottom: 30px;
      }
      .contact {
        flex-basis: auto;
        order: 2;
        padding-left: 170px;
        &__operator {
          height: 90%;
        }
      }
    }
  }
  &__title {
    padding-top: 58px;
    @include rfs(20);
    font-weight: bold;
  }
  .contact {
    display: flex;
    justify-content: center;
    position: relative;
    min-height: 340px;
    padding-left: 210px;
    @media only screen and (max-width: 360px) {
      min-height: auto;
      padding-left: 0;
      margin-bottom: 60px;
    }
    .contact__data {
      display: flex;
      flex-direction: column;
      z-index: 10;
    }
    &__operator {
      height: 108%;
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 0;
      @media only screen and (max-width: 360px) {
        display: none;
      }
    }
    &__item {
      margin-top: 14px;
      display: flex;
    }
  }
  .reviews {
    display: flex;
    flex-direction: column;
    align-items: center;
    &__data {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
    }
    &__pic {
      max-height: 173px;
    }
    &__rating {
      margin: 0 2em;
    }
    &__mark {
      font-size: 80px;
      line-height: 1;
      font-weight: bold;
      color: #ffffff;
    }
    &__star {
      width: 26.7px;
      height: 25.3px;
      margin-top: 5px;
    }
    @media only screen and (max-width: 580px) {
      &__rating {
        margin: 0 1.5em;
      }
      &__pic {
        max-height: 133px;
      }
      &__mark {
        font-size: 52px;
      }
      &__star {
        margin-top: 5px;
        width: 20px;
        height: 20px;
      }
    }
    &__link {
      position: absolute;
      bottom: -13px;
      left: 0;
      width: 100%;
      height: auto;
      &:hover {
        color: #f99d0b;
        span {
          border-color: #f99d0b;
        }
      }
      span {
        border-bottom: 1px solid #fff;
      }
    }
    &__pic--left {
      -moz-transform: scaleX(-1);
      -o-transform: scaleX(-1);
      -webkit-transform: scaleX(-1);
      transform: scaleX(-1);
      filter: FlipH;
      -ms-filter: "FlipH";
    }
  }
}

// end contact section
// footer
.footer {
  color: #fff;
  background-color: #21547d;
  .footer__menu {
    padding: 3em 0 2.4em;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    @media only screen and (max-width: 768px) {
      justify-content: space-around;
    }
    @media only screen and (max-width: 480px) {
      flex-direction: column;
      align-items: center;
    }
  }
  .menu__column {
    margin-bottom: 1em;
    margin-right: 10vw;
    &:last-child {
      margin: 0;
    }
    @media only screen and (max-width: 768px) {
      margin-right: 0;
      flex-basis: 35%;
    }
    @media only screen and (max-width: 480px) {
      flex-basis: 100%;
      text-align: center;
    }
  }
  .menu__title {
    margin-bottom: 0.6em;
    @include rfs(18);
    font-weight: bold;
    color: #f58b0e;
    @media only screen and (max-width: 768px) {
      margin-bottom: 0.1em;
    }
  }
  .menu__link {
    display: block;
    line-height: 1.5;
    @include rfs(14);
    &:hover {
      color: #f99d0b;
    }
  }
  .footer__copy {
    padding: 9px 0 9px;
    text-align: center;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: bold;
    color: #ffffff;
    background-color: #184468;
  }
}

// end footer
// end custom stars
@import "media";